import Tag from '@/componets/uni/uni-tag'
export default {
  name: 'scenicDetailInfo',
  props: {
    scenicInfo: Object
  },
  components: {
	Tag
  },
  methods: {
    startScenicInfo () {
		uni.navigateTo({
			url: this.$mRouter.scenicInfo({
				scenicId: this.$parent.$routeParams.scenicId
			})
		})
    },
	delHtmlTag (str) {
	  if (str) {
	    return str.replace(/<[^>]+>/g, '')
	  }
	  return ''
	},
	callPhone () {
		uni.makePhoneCall({
			phoneNumber: this.scenicInfo.tel
		})
	}
  }
}
